/* - Header */
.header {
	padding: 0 0 0 50px;
	position: fixed;
	left: 50px;
	right: 50px;
	top: 40px;
	height: 80px;
	z-index: 100;
	background: $extra_bg;
	@include border-radius(80px);
	@include transition(all 0.7s cubic-bezier(0.165, 0.85, 0.45, 1));
	.logo {
		position: relative;
		float: left;
		width: 200px;
		height: 80px;
		display: flex;
		font-size: 0;
		line-height: 1.2;
		font-weight: 800;
		color: $white_color;
		align-items: center;
		a {
			color: $white_color;
		}
		img {
			max-width: 100%;
		}
		img.logo-img {
			margin-right: 30px;
			width: auto;
			height: auto;
			max-width: 36px;
			display: inline-block;
		}
		.logo-lnk {
			font-size: $logo_size;
			text-decoration: none;
			display: inline-block;
			max-width: 100px;
		}
	}
	.download-cv-btn {
		float: right;
		margin-top: 8px;
		margin-right: 8px;
		border-color: $white_color;
		&:hover {
			.icon {
				@include transform(translateY(0));
			}
		}
	}
	.top-menu {
		margin-right: 50px;
		float: right;
		ul {
			margin: 0;
			padding: 0;
			list-style: none;
			height: 80px;
			display: flex;
			align-items: center;
			li {
				margin-left: 40px;
				display: inline-block;
				vertical-align: top;
				position: relative;
				a {
					position: relative;
					overflow: hidden;
					display: block;
					font-size: $small_size;
					line-height: 50px;
					color: $white_color;
					font-weight: 800;
					text-decoration: none;
					text-transform: uppercase;
					@include transition(color 0.7s cubic-bezier(0.165, 0.85, 0.45, 1));
				}
				&:first-child {
					margin-left: 0;
				}
				&:hover > .sub-menu,
				&:hover > .children {
					opacity: 1;
					pointer-events: auto;
				}
				&.current-menu-item > a {
					color: $active_color;
				}
				&:hover > a {
					&:hover {
						.animated-button {
							em {
								transform: translateY(calc(16px * -1));
							}
						}
					}
				}
				&.menu-item-has-children > a:after {
					content: "\f078";
					margin-left: 5px;
					position: relative;
					top: 0;
					display: inline-block;
					font-family: 'Font Awesome 5 Free';
					font-weight: 900;
					font-size: 1em;
					-moz-osx-font-smoothing: grayscale;
					-webkit-font-smoothing: antialiased;
					display: inline-block;
					font-style: normal;
					font-variant: normal;
					text-rendering: auto;
					line-height: 1;
				}
			}
		}
	}
	.top-menu-nav .sub-menu,
	.top-menu-nav .children {
		position: absolute;
		left: 0;
		top: 50px;
		padding: 10px 0;
		width: 190px;
		height: auto;
		display: block;
		background: $extra_bg;
		z-index: 50;
		opacity: 0;
		pointer-events: none;
		@include transition(all 0.3s ease 0s);
		@include border-radius(3px);
		@include box-shadow(0 5px 50px rgba(0,0,0,0.3));
	}
	.top-menu ul li:last-child .sub-menu,
	.top-menu-nav .children {
		right: 0;
		left: auto;
	}
	.top-menu-nav .sub-menu:before,
	.top-menu-nav .children:before {
		content: '';
		position: absolute;
		left: 10px;
		top: -12px;
		border: 6px solid transparent;
		border-bottom: 6px solid $extra_bg;
	}
	.top-menu ul li:last-child .sub-menu:before,
	.top-menu-nav .children:before {
		right: 10px;
		left: auto;
	}
	.top-menu-nav .sub-menu li,
	.top-menu-nav .children li {
		margin-left: 0;
		padding: 10px 20px;
		display: block;
		border-bottom: 1px solid rgba(255,255,255,0.1);
		height: auto;
	}
	.top-menu-nav .sub-menu li:last-child,
	.top-menu-nav .children li:last-child {
		border-bottom: none;
	}
	.top-menu-nav .sub-menu li a,
	.top-menu-nav .children li a {
		margin: 0;
		display: block;
		font-size: $extra_small_size;
		line-height: 1.4;
		color: $white_color;
	}
	.top-menu-nav .sub-menu li a:hover,
	.top-menu-nav .children li a:hover,
	.top-menu-nav .sub-menu li.active a,
	.top-menu-nav .children li.active a {
		color: $active_color;
	}
	.menu-btn {
		padding: 20px;
		position: relative;
		margin: 0 auto;
		margin-top: 8px;
		width: 64px;
		height: 64px;
		line-height: 62px;
		display: none;
		border: 1px solid rgba(255,255,255,0.7);
		@include border-radius(64px);
		&:before,
		&:after {
			content: '';
			position: absolute;
			top: 22px;
			left: 20px;
			right: 20px;
			height: 2px;
			background: $white_color;
			@include transition(all 0.3s ease 0s);
		}
		&:after {
			top: auto;
			bottom: 22px;
		}
		span {
			position: absolute;
			top: 50%;
			left: 25px;
			right: 25px;
			height: 2px;
			background: $white_color;
			margin-top: -1px;
			@include transition(all 0.3s ease 0s);
		}
	}
}

/* header active */
.header.active {
	top: 0;
	overflow-y: scroll;
	-webkit-overflow-scrolling: touch;
	height: 100%;
	background: $default_bg;
	.menu-btn {
		&:before {
			opacity: 0;
		}
		&:after {
			bottom: 30px;
			@include transform(rotate(-45deg));
		}
		span {
			left: 20px;
			right: 20px;
			@include transform(rotate(45deg));
		}
	}
	.top-menu {
		height: auto;
		opacity: 1;
		visibility: visible;
		ul li .sub-menu,
		ul li .children {
			display: block;
		}
	}
}

@media (max-width: 1199px) {
	.header {
		padding: 0 30px;
		left: 0;
		right: 0;
		top: 20px;
		height: 80px;
		background: none;
		@include border-radius(0);
	}
	.header.fixed {
		top: 0;
		background: #3a3e44;
	}
	.header .download-cv-btn {
		display: none;
	}
	.header .top-menu-nav {
		display: block;
	}
	.header .top-menu {
		position: relative;
		top: 0;
		left: 0;
		margin-top: 0;
		margin-left: 0;
		margin-right: 0;
		float: none;
		width: auto;
		height: 0px;
		text-align: center;
		opacity: 0;
		visibility: hidden;
		@include transition(opacity 0.35s cubic-bezier(0.165, 0.85, 0.45, 1));
	}
	.header.active .top-menu {
		padding-top: 30px;
		clear: both;
	}
	.header .top-menu .menu-top-menu-container {
		display: block;
	}
	.header .top-menu ul {
		display: block;
	}
	.header .top-menu ul li {
		margin-left: 0;
		display: block;
	}
	.header .top-menu ul li a {
		font-size: 20px;
	}
	.header .top-menu-nav .sub-menu, .header .top-menu-nav .children {
		position: relative;
		left: 0;
		top: 0;
		margin: 20px 0;
		width: auto;
		text-align: center;
		pointer-events: none;
		display: block;
		opacity: 0;
		visibility: hidden;
	}
	.header.active .top-menu-nav .sub-menu, .header.active .top-menu-nav .children {
		opacity: 1;
		visibility: visible;
		pointer-events: auto;
	}
	.header .top-menu-nav .sub-menu:before, .header .top-menu-nav .children:before {
		margin-left: -6px;
		left: 50%;
	}
	.header .top-menu-nav .sub-menu li .sub-menu, .header .top-menu-nav .children li .children {
		left: 0;
		top: 0;
		margin: 0 -20px;
		background: #f4f4f4;
		@include box-shadow(none);
	}
	.header .top-menu-nav .sub-menu li .sub-menu:before, .header .top-menu-nav .children li .children:before {
		left: 50%;
		top: -12px;
		border: 6px solid transparent;
		border-right: 6px solid transparent;
		border-bottom: 6px solid $white_color;
	}
	.header .top-menu ul li:last-child .sub-menu:before, .header .top-menu-nav .children:before {
		right: 50%;
		left: auto;
		margin-left: 0;
		margin-right: -6px;
	}
	.header .menu-btn {
		display: block;
		float: right;
	}
}

