/* - Background Option */
.background-bg {
	position: fixed;
	top: 0;
	left: 0;
	width: 45%;
	height: 100%;
}

.background-filter {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	background-repeat: no-repeat;
	background-position: center center;
	background-size: cover;
	background-color: $active_profile_color;
	clip-path: circle(75% at 10% 45%);
	-webkit-clip-path: circle(75% at 10% 45%);
	.background-img {
		-webkit-filter: grayscale(100%);
		filter: grayscale(100%);
		mix-blend-mode: luminosity;
	}
	&.gradient {
		&:after {
			content: '';
			position: absolute;
			left: 0;
			top: 0;
			width: 100%;
			height: 100%;
			background: -moz-radial-gradient(center, ellipse cover, rgba(0,0,0,0.5) 0%, #373b40 90%);
			background: -webkit-radial-gradient(center, ellipse cover, rgba(0,0,0,0.5) 0%, #373b40 90%);
			background: radial-gradient(ellipse at center, rgba(0,0,0,0.5) 0%, #373b40 90%);
		}
		.background-img {
			-webkit-filter: none;
			filter: none;
			mix-blend-mode: unset;
		}
	}
	&.circle {
		clip-path: circle(75% at 10% 45%);
		-webkit-clip-path: circle(75% at 10% 45%);
		.background-img {
			-webkit-filter: none;
			filter: none;
			mix-blend-mode: unset;
		}
	}
}

.background-img {
	position: absolute;
	left: 0;
	top: 0;
	width: 100%;
	height: 100%;
	background-repeat: no-repeat;
	background-position: center center;
	background-size: cover;
}

@media (max-width: 1199px) {
	.background-bg {
		position: relative;
		width: 100%;
		height: 70vh;
	}
	.background-filter.circle {
		clip-path: circle(100% at 50% 0%);
		-webkit-clip-path: circle(100% at 50% 0%);
	}
}

@media (max-width: 720px) {
	.background-bg {
		height: 450px;
	}
}

/* - Section */
.section {
	position: relative;
	overflow: hidden;
	padding-left: 8%;
	padding-right: 8%;
	padding-top: 70px;
	padding-bottom: 70px;
	border-bottom: 1px solid #585d65;
	z-index: 12;
	&:last-child {
		border-bottom: none;
	}
	.content {
		position: relative;
		max-width: 720px;
	}
	.titles {
		position: relative;
		margin: 0 0 60px 0;
		.title {
			margin-bottom: 10px;
			max-width: 70%;
			font-size: $section_title_size;
			line-height: 1.2;
			color: $white_color;
			font-weight: 800;
			@include transition(all 0.3s ease 0s);
		}
		.subtitle {
			font-size: $default_size;
			line-height: 1.2;
			color: $active_color;
			font-weight: 600;
			text-transform: uppercase;
		}
	}
}

.content-carousel {
	position: relative;
	z-index: 2;
	.owl-carousel {
		.item {
			img {
				width: auto;
				max-width: 100%;
			}
		}
	}
	.navs {
		position: absolute;
		left: auto;
		right: 0;
		top: -150px;
		height: 1px;
		z-index: 3;
		font-size: 0;
		&:before {
			content: '';
			position: absolute;
			left: 18px;
			top: 32px;
			width: 1000%;
			height: 1px;
			background: $lines_color;
		}
		.prev,
		.next {
			margin-left: 15px;
			margin-top: 0;
			position: relative;
			top: 0;
			left: 0;
			right: auto;
			width: 64px;
			height: 64px;
			line-height: 62px;
			background: $default_bg;
			font-size: $default_size;
			color: #FFF;
			text-decoration: none;
			text-align: center;
			cursor: pointer;
			border: 1px solid $lines_color;
			@include border-radius(64px);
			@include transition(all 0.3s ease 0s);
		}
	}
}

@media (max-width: 1199px) {
	.section {
		padding-left: 30px;
		padding-right: 30px;
	}
}

@media (max-width: 720px) {
	.section {
		padding-top: 50px;
		padding-bottom: 50px;
		padding-left: 30px;
		padding-right: 30px;
