	}
}

/* - Started */
.section.started {
	margin: 0;
	padding-top: 0;
	padding-left: 0;
	padding-bottom: 0;
	position: relative;
	overflow: visible;
	height: 100vh;
	z-index: 11;
	.h-title {
		margin: 40px 0 0 -11vw;
		padding: $title_size;
		font-size: $title_size;
		line-height: 1.2;
		font-weight: 800;
		color: $white_color;
		background: $default_bg;
		word-break: break-word;
		-ms-word-wrap: break-word;
		word-wrap: break-word;
		@include border-radius(264px);
		strong {
			color: $active_color;
		}
	}
	.started-content {
		padding-left: 8%;
		max-width: 500px;
		.h-subtitles {
			margin: 0 0 30px 0;
			height: 18px;
		}
		.h-subtitle,
		.typed-subtitle,
		.typed-cursor {
			display: inline-block;
			font-size: $default_size;
			line-height: 1.2;
			color: $active_color;
			font-weight: 600;
			text-transform: uppercase;
		}
		.typed-cursor {
			margin-left: 3px;
		}
		.h-text {
			margin: 0 0 30px 0;
			font-size: $default_size;
			color: $default_color;
		}
	}
	.btn {
		margin-right: 15px;
	}
	.mouse-btn {
		padding: 0;
		width: 64px;
		font-size: $large_size;
		text-align: center;
		.icon {
			margin-left: 0;
			position: relative;
			top: -5px;
			left: 0;
			animation: mouse-anim 1s ease-out 0s infinite;
		}
		&:hover {
			.icon {
				@include transform(translateX(0));
			}
		}
	}
}

.started-carousel {
	position: absolute;
	left: 0;
	top: 0;
	width: 100%;
	height: 100%;
	.swiper-container,
	.swiper-wrapper {
		height: 100%;
	}
	.swiper-slide {
		opacity: 1 !important;
		&.first {
			z-index: 2;
		}
	}
	.background-filter {
		clip-path: polygon(0 0, 100% 0, 100% 100%, 0 100%);
		-webkit-clip-path: polygon(0 0, 100% 0, 100% 100%, 0 100%);
		@include transition(1s all cubic-bezier(0.165, 0.85, 0.45, 1) 0s);
	}
	.swiper-slide.swiper-clip-active .background-filter {
		clip-path: polygon(0 0, 100% 0%, 100% 0, 0 0);
		-webkit-clip-path: polygon(0 0, 100% 0%, 100% 0, 0 0);
	}
}

