		}
	}
	&.list {
		.name {
			padding-left: 35px;
			position: relative;
			&:before {
				content: "\f00c";
				position: absolute;
				left: 0;
				top: 3px;
				display: inline-block;
				color: $active_color;
				font-family: 'Font Awesome 5 Free';
				font-weight: 900;
				-moz-osx-font-smoothing: grayscale;
				-webkit-font-smoothing: antialiased;
				display: inline-block;
				font-style: normal;
				font-variant: normal;
				text-rendering: auto;
				line-height: 1;
			}
		}
		.progress {
			display: none;
		}
	}
	&.dotted {
		.progress {
			height: 14px;
			background: transparent!important;
			.percentage {
				position: relative;
				overflow: hidden;
				top: 0;
				height: 14px;
				background: transparent!important;
				z-index: 2;
			}
			.dg,
			.da {
				position: absolute;
				left: 0;
				top: 0;
				width: 100%;
				display: flex;
				justify-content: space-between;
				height: 14px;
				span {
					display: block;
					width: 14px;
					height: 14px;
					background: $lines_color;
					@include border-radius(14px);
				}
			}
			.da {
				top: 0;
				span {
					background: $active_color;
				}
			}
		}
	}
	&.circles {
		ul {
			li {
				width: 33.333%;
			}
		}
		.progress {
			margin: 0 0 30px 0;
			position: relative;
			font-size: 90px;
			width: 90px;
			height: 90px;
			background: $lines_color;
			@include border-radius(90px);
			&:after {
				content: '';
				position: absolute;
				top: 0.04em;
				left: 0.04em;
				width: 0.92em;
				height: 0.92em;
				display: block;
				background-color: $default_bg;
				@include border-radius(50%);
			}
			span {
				position: absolute;
				width: 100%;
				left: 0;
				top: 0;
				width: 100%;
				height: 100%;
				font-size: $small_size;
				color: $active_color;
				font-weight: 600;
				line-height: 90px;
				text-align: center;
				white-space: nowrap;
				z-index: 2;
			}
			.percentage {
				display: none;
			}
			.slice {
				position: absolute;
				width: 1em;
				height: 1em;
				clip: rect(0em, 1em, 1em, 0.5em);
				-webkit-clip: rect(0em, 1em, 1em, 0.5em);
			}
		}
		.single-post-text {
			margin-bottom: 0;
		}
	}
}

